import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  TextField,
  Typography,
  LinearProgress,
} from '@material-ui/core';

import {
  GuestLayout,
} from '../components/UI';

import accountServices from '../services/account';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function ResetPasswordRequest(): JSX.Element {
  const classes = useStyles();
  const [code, setCode] = React.useState<string>('');
  const [isLoading, setIsLoading] = React.useState(false);
  const [isError, setIsError] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState<string>('');
  const [isSuccess, setIsSuccess] = React.useState(false);

  const handleSubmit = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();
    setIsError(false);
    setErrorMessage('');
    if (code === '') {
      setIsError(true);
      setErrorMessage('Vui lòng nhập mã số thuế cần đặt lại mật khẩu!');
    }
    else {
      setIsLoading(true);
      try {
        await accountServices.createResetPassword(code);
        setIsSuccess(true);
        setIsLoading(false);
      }
      catch {
        setIsError(true);
        setErrorMessage('Không thể tạo yêu cầu đặt lại mật khẩu. Vui lòng thử lại sau!');
        setIsLoading(false);
      }
    }
  }

  return (
    <GuestLayout>
      <Typography variant="h6" color="primary" align="center">
        Yêu cầu đặt lại mật khẩu
      </Typography>
      {isSuccess ? (
        <Typography variant="body1">
          Yêu cầu đặt lại mật khẩu đã gửi đến email đã đăng ký trên hệ thống. Vui lòng truy cập và làm theo hướng dẫn để cập nhật lại mật khẩu.
        </Typography>
      ) : (
          <form className={classes.form} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              label="Mã nhân viên"
              name="number"
              autoComplete="number"
              value={code}
              onChange={(event) => { setCode(event.target.value); }}
              autoFocus
            />
            {isLoading
              ? <LinearProgress />
              : (
                <>
                  {isError && <Typography color="error" variant="subtitle2">{errorMessage}</Typography>}
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={handleSubmit}
                  >
                    Gửi yêu cầu
                </Button>
                </>
              )
            }
          </form>
        )}
    </GuestLayout>
  );
}
