import React from 'react';
import {
  Typography,
  LinearProgress,
  Grid,
} from '@material-ui/core';

import {
  TextField,
  Checkbox,
  IconButton,
} from '../components/UI';

import { LayoutContext } from '../components/Layout';
import accountService, { RegisterModel } from '../services/account';
import categoryService, { CategoryModel } from '../services/category';

type State = {
  isLoading: boolean;
  isError: boolean;
  errorMessage: string;
}

type CategoryState = CategoryModel & {
  isChecked: boolean,
}

type FormState = RegisterModel & {
  confirmPassword: string;
}

const initialState: FormState = {
  address: '',
  categories: [],
  code: '',
  email: '',
  longTextName: '',
  password: '',
  confirmPassword: '',
  phone: '',
  shortTextName: '',
}

export default function Register(): JSX.Element {
  const { changeIndex } = React.useContext(LayoutContext);
  const [model, setModel] = React.useState<FormState>(initialState);
  const [categories, setCategories] = React.useState<CategoryState[]>([]);
  const [state, setState] = React.useState<State>({
    errorMessage: '',
    isLoading: false,
    isError: false,
  })

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setModel({ ...model, [event.target.name]: event.target.value });
  };

  const handleChecked = (id: number, checked: boolean) => {
    setCategories(categories.map(c => {
      if (c.id == id) {
        c.isChecked = checked;
        return c;
      }

      return c;
    }));
  }

  const handleSubmit = async () => {
    if (model.address === '' ||
      model.code === '' ||
      model.confirmPassword === '' ||
      model.email === '' ||
      model.longTextName === '' ||
      model.password === '' ||
      model.phone === '' ||
      model.shortTextName === '') {
      setState({ ...state, isError: true, errorMessage: 'Vui lòng điền đầy đủ các thông tin trước khi gửi đăng ký!' });
      return;
    }

    if (model.password !== model.confirmPassword) {
      setState({ ...state, isError: true, errorMessage: 'Xác nhận mật khẩu không chính xác!' });
      return;
    }

    if (categories.filter(x => x.isChecked).length === 0) {
      setState({ ...state, isError: true, errorMessage: 'Vui lòng chọn ít nhất một lĩnh vực hoạt động!' });
      return;
    }

    setState({ isLoading: true, isError: false, errorMessage: '' });
    try {
      const data: RegisterModel = {
        address: model.address,
        categories: categories.filter(x => x.isChecked).map(x => x.id),
        code: model.code,
        email: model.email,
        longTextName: model.longTextName,
        password: model.password,
        phone: model.phone,
        shortTextName: model.shortTextName,
      }
      await accountService.register(data);
      setState({ ...state, isLoading: false });
    }
    catch (error) {
      setState({ isLoading: false, isError: true, errorMessage: error.response.data });
    }
  };

  const getCategories = async () => {
    try {
      const data = await categoryService.getAll();
      setCategories(data.map(c => ({ ...c, isChecked: false })));
    }
    catch {
      setCategories([]);
    }
  }

  React.useEffect(() => {
    changeIndex(2);
    getCategories();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Grid container spacing={2} alignItems="flex-start">
      <Grid item container spacing={1} xs={6}>
        <Grid item xs={12}>
          <Typography variant="h6">
            Đăng ký nhà cung cấp
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            label="Mã số thuế"
            name="code"
            value={model.code}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="password"
            label="Mật khẩu"
            type="password"
            value={model.password}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="confirmPassword"
            label="Xác nhận mật khẩu"
            type="password"
            value={model.confirmPassword}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="address"
            label="Địa chỉ"
            type="text"
            value={model.address}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="email"
            label="Email"
            type="email"
            value={model.email}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="phone"
            label="Điện thoại liên hệ"
            type="phone"
            value={model.phone}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="longTextName"
            label="Tên nhà cung cấp"
            type="text"
            value={model.longTextName}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            name="shortTextName"
            label="Tên rút gọn"
            type="text"
            value={model.shortTextName}
            onChange={handleChange}
          />
        </Grid>
        <Grid container item xs={12} spacing={2}>
          {state.isError && (
            <Grid item xs={12}>
              <Typography color="error" variant="subtitle2">
                {state.errorMessage}
              </Typography>
            </Grid>
          )}
          <Grid item xs={12}>
            <IconButton
              variant="contained"
              color="primary"
              text="Đăng ký"
              icon="user-plus"
              onClick={handleSubmit}
              disable={state.isLoading}
            />
          </Grid>
        </Grid>
      </Grid>
      <Grid item container xs={6}>
        <Grid item xs={12}>
          <Typography variant="h6">
            Lĩnh vực hoạt động
          </Typography>
        </Grid>
        <Grid item xs={12}>
          {categories.map(category => (
            <Checkbox
              key={category.id}
              label={category.name}
              name={category.name}
              checked={category.isChecked}
              onChange={(_event, value) => { handleChecked(category.id, value) }}
            />
          ))}
        </Grid>
      </Grid>
    </Grid>
  );
}
