import React from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  TextField,
  Typography,
  LinearProgress,
} from '@material-ui/core';

import {
  GuestLayout,
} from '../components/UI';

import accountServices, { ResetPasswordModel } from '../services/account';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

type ChangePasswordState = {
  newPassword: string;
  confirmPassword: string;
}

export default function ResetPassword(): JSX.Element {
  const classes = useStyles();
  const { resetPasswordToken }: never = useParams();
  const history = useHistory();
  const [state, setState] = React.useState<ChangePasswordState>({
    newPassword: '',
    confirmPassword: '',
  });
  const [isLoading, setIsLoading] = React.useState(false);
  const [isError, setIsError] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState<string>('');
  const [isSuccess, setIsSuccess] = React.useState(false);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setState({ ...state, [event.target.name]: event.target.value });
  };

  const handleSubmit = async (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();
    setIsError(false);
    setErrorMessage('');
    if (state.newPassword !== state.confirmPassword) {
      setIsError(true);
      setErrorMessage('Xác nhận mật khẩu không chính xác!');
    }
    else {
      setIsLoading(true);
      try {
        const model: ResetPasswordModel = {
          token: resetPasswordToken,
          newPassword: state.newPassword,
        };
        await accountServices.resetPassword(model);
        setIsSuccess(true);
        setIsLoading(false);
      }
      catch {
        setIsError(true);
        setErrorMessage('Không thể đặt lại mật khẩu. Vui lòng thử lại sau!');
        setIsLoading(false);
      }
    }
  }

  return (
    <GuestLayout>
      <Typography variant="h6" color="primary" align="center">
        Đặt lại mật khẩu
      </Typography>
      {isSuccess ? (
        <>
          <Typography variant="body1">
            Đặt lại mật khẩu thành công. Nhấn vào nút bên dưới để đến trang đăng nhập.
        </Typography>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={() => { history.push('/'); }}
          >
            Đến trang đăng nhập
          </Button>
        </>
      ) : (
          <form className={classes.form} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              label="Mật khẩu mới"
              type="password"
              name="newPassword"
              value={state.newPassword}
              onChange={handleChange}
              autoFocus
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              label="Xác nhận mật khẩu"
              type="password"
              name="confirmPassword"
              value={state.confirmPassword}
              onChange={handleChange}
            />
            {isLoading
              ? <LinearProgress />
              : (
                <>
                  {isError && <Typography color="error" variant="subtitle2">{errorMessage}</Typography>}
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={handleSubmit}
                  >
                    Đặt lại mật khẩu
                </Button>
                </>
              )
            }
          </form>
        )}
    </GuestLayout>
  );
}
