import {
  Actions,
  LOGIN_REQUEST,
  LOGIN_FAILURE,
  LOGOUT,
  VERIFY_REQUEST,
  VERIFY_FAILURE,
  VERIFY_SUCCESS,
} from './constant';
import { AuthState, initialState } from './types';

export default function reducer(state = initialState, action: Actions): AuthState {
  switch (action.type) {
    case LOGIN_REQUEST:
      return {
        ...initialState,
        isVerifying: false,
        isLoading: true,
      };

    case LOGIN_FAILURE:
      return {
        ...initialState,
        isError: true,
        isVerifying: false,
        errorMessage: action.error,
      };

    case VERIFY_REQUEST:
      return {
        ...initialState,
        isVerifying: true,
      };

    case VERIFY_SUCCESS:
      return {
        ...initialState,
        isVerifying: false,
        user: action.payload,
      };

    case VERIFY_FAILURE:
      return {
        ...initialState,
        isVerifying: false,
      };

    case LOGOUT:
      return {
        ...initialState,
        isVerifying: false,
      };

    default:
      return state;
  }
}
